importScripts('https://storage.googleapis.com/workbox-cdn/releases/6.4.1/workbox-sw.js');  // <-- workbox  CDN 

// Workbox   
workbox.setConfig({
  debug: true,
});

// Vue-Cli     Workbox  
workbox.precaching.precacheAndRoute(self.__WB_MANIFEST);  // <-- self.__WB_MANIFEST 

//   
workbox.routing.registerRoute(
  new RegExp('https://firebasestorage.googleapis.com/v0/b/pwa-camera.appspot.com/.*'),
  new workbox.strategies.StaleWhileRevalidate({
      cacheName: 'camera-images',
      plugins: [
          new workbox.expiration.ExpirationPlugin ({   // <-- ExpirationPlugin  'E' 
            maxEntries: 60,
            maxAgeSeconds: 365 * 24 * 60 * 60, // 1 
          }),
        ],
  })
);